import { useState, useEffect, useCallback } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import { Box, Breadcrumbs, Container, Grid, Link, Typography, Tab, Tabs, } from '@material-ui/core';
// import { customerApi } from '../../__fakeApi__/customerApi';
// import { VehiclesListTable } from '../../../components/dashboard/concessionaire';
import useMounted from '../../../hooks/useMounted';
import ChevronRightIcon from '../../../icons/ChevronRight';
// import DownloadIcon from '../../../icons/Download';
// import PlusIcon from '../../../icons/Plus';
// import UploadIcon from '../../../icons/Upload';
import useSettings from '../../../hooks/useSettings';
import gtm from '../../../lib/gtm';
import DriverListTable from '../../../components/dashboard/driver/DriverListTable';
// import useAuth from '../../../hooks/useAuth';
// import avatar from '../../../assets/images/Mac/logo.jpg';
import DriversRepository from '../../../repositories/drivers.repository';

const tabs = [
  {
    label: 'Activos',
    value: 'activos'
  },
  {
    label: 'Inactivos',
    value: 'inactivos'
  },
  {
    label: 'Solicitudes',
    value: 'solicitudes'
  }
];

const DriverList = () => {
  const mounted = useMounted();
  const driversRepository = new DriversRepository();
  const { settings } = useSettings();
  const [currentTab, setCurrentTab] = useState('activos');
  const [customers, setCustomers] = useState([]);
  const [inactivos, setInactivos] = useState([]);
  const [solicitudes, setSolicitudes] = useState([]);
  // const { user } = useAuth();
  // const navigate = useNavigate();

  const handleTabsChange = (event, value) => {
    setCurrentTab(value);
  };

  // const getActives = () => {
  //   setCustomers(['drivers activos']);
  //   console.log('drivers activos');
  // };

  // const getInactives = () => {
  //   console.log('drivers inactivos');
  //   setInactivos(['drivers inactivos']);
  // };

  useEffect(() => {
    // getActives();
    // getInactives();
    gtm.push({ event: 'page_view' });
  }, []);

  const getCustomers = useCallback(async () => {
    try {
      // const data = await customerApi.getCustomers();

      // if (mounted.current) {
      //   setCustomers(data);
      // }
      driversRepository.getAllUsers().then((res) => {
        const solicitudesNew = [];
        const allACT = [];
        res.forEach((element) => {
          if (element.role_id === 'DRV' && element.status_id === 'REV') {
            solicitudesNew.push(element);
          } else if (element.role_id === 'DRV' && element.status_id === 'ACT') {
            allACT.push(element);
          }
        });
        console.log('SOLICITUDES', solicitudesNew);
        setCustomers(allACT);
        setSolicitudes(solicitudesNew);
      });
      setInactivos([]);
    } catch (err) {
      console.error(err);
    }
  }, [mounted]);

  useEffect(() => {
    getCustomers();
  }, [getCustomers]);

  return (
    <>
      <Helmet>
        <title>Dashboard: Drivers | Mac Admin</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: 'background.default',
          minHeight: '100%',
          py: 8
        }}
      >
        <Container maxWidth={settings.compact ? 'xl' : false}>
          <Grid
            container
            justifyContent="space-between"
            spacing={3}
          >
            <Grid item>
              <Typography
                color="textPrimary"
                variant="h5"
              >
                Lista de Drivers
              </Typography>
              <Breadcrumbs
                aria-label="breadcrumb"
                separator={<ChevronRightIcon fontSize="small" />}
                sx={{ mt: 1 }}
              >
                <Link
                  color="textPrimary"
                  component={RouterLink}
                  to="/dashboard"
                  variant="subtitle2"
                >
                  Dashboard
                </Link>
                <Typography
                  color="textSecondary"
                  variant="subtitle2"
                >
                  Lista de Drivers
                </Typography>
              </Breadcrumbs>
              {/* <Box
                sx={{
                  mb: -1,
                  mx: -1,
                  mt: 1
                }}
              >
                <Button
                  color="primary"
                  startIcon={<UploadIcon fontSize="small" />}
                  sx={{ m: 1 }}
                >
                  Import
                </Button>
                <Button
                  color="primary"
                  startIcon={<DownloadIcon fontSize="small" />}
                  sx={{ m: 1 }}
                >
                  Export
                </Button>
              </Box> */}
            </Grid>
            {/* <Grid item>
              <Box sx={{ m: -1 }}>
                <Button
                  color="primary"
                  startIcon={<PlusIcon fontSize="small" />}
                  sx={{ m: 1 }}
                  component={RouterLink}
                  variant="contained"
                  to="/dashboard/drivers/crear"
                >
                  Agregar Driver
                </Button>
              </Box>
            </Grid> */}
          </Grid>
          <Box sx={{ mt: 3 }}>
            <Box sx={{ mb: 3 }}>
              <Tabs
                indicatorColor="primary"
                onChange={handleTabsChange}
                scrollButtons="auto"
                textColor="primary"
                value={currentTab}
                variant="scrollable"
              >
                {tabs.map((tab) => (
                  <Tab
                    key={tab.value}
                    label={tab.label}
                    value={tab.value}
                  />
                ))}
              </Tabs>
            </Box>
            {currentTab === 'activos' && (
              <DriverListTable customers={customers} />
            )}
            {currentTab === 'inactivos' && (
              <DriverListTable customers={inactivos} />
            )}
            {currentTab === 'solicitudes' && (
              <DriverListTable customers={solicitudes} />
            )}
          </Box>
        </Container>
      </Box>
    </>
  );
};

/* eslint-disable eol-last */
export default DriverList;